import React from "react"
import { graphql, Link } from "gatsby"

import { MDXProvider } from "@mdx-js/react"
import { MDXRenderer } from "gatsby-plugin-mdx"

import Layout from "@components/layout/layout"

// MDX Components
import { H1, H2, H3, H4, H5, H6 } from "@components/header/header.js";
import Button from "@components/customButtons/button.js";
import GridContainer from "@components/Grid/GridContainer.js";
import GridItem from "@components/Grid/GridItem.js";

// Provide common components here
const shortcodes = { GridContainer, GridItem, Button, Link, H1, H2, H3, H4, H5, H6 }


/*
children: 
location: {hash:, host:, hostname:, href, key, orgin, pathname, port, protocol, search, state:{key:}}
navigate: ƒ navigate(to, options)
pageContext: {slug: }
pageResources: {json: {…}, page: {…}, staticQueryResults: {…}, component: ƒ}
params: {}
path: "/about"
pageContext: {slug: "/about"} //Based off gatsby-node create page
uri: "/about"
*/
export default function TemplatePage({ data, pageContext}) {
  const { node } = data
    
  return (
    <Layout pageNode={node} >
      <MDXProvider components={shortcodes}>
        <MDXRenderer pageNode={node}>{pageContext.body}</MDXRenderer>
      </MDXProvider>
    </Layout >
  )
}

export const query = graphql`
 query($pageID: String!) {
    node:threeMysticApesPage(id: {eq: $pageID}) {
      context: page {
        slug
        ariaLabel
        button
        description
        keywords
        meta {
          content
          name
          property
        }
        pageStyle {
          name
          style
        }
        title
      }
    }
  }
`